"use strict";
import { usePoolActiveLiquidity } from "hooks/usePoolTickData";
import { useCallback, useMemo } from "react";
export function useDensityChartData({
  poolId,
  currencyA,
  currencyB,
  feeAmount,
  invertPrices,
  version,
  chainId,
  tickSpacing,
  hooks
}) {
  const { isLoading, error, data } = usePoolActiveLiquidity({
    currencyA,
    currencyB,
    version,
    poolId,
    feeAmount,
    chainId,
    tickSpacing,
    hooks
  });
  const formatData = useCallback(() => {
    if (!data?.length) {
      return void 0;
    }
    const newData = [];
    for (let i = 0; i < data.length; i++) {
      const t = data[i];
      const price0 = invertPrices ? t.sdkPrice.invert().toSignificant(8) : t.sdkPrice.toSignificant(8);
      const chartEntry = {
        activeLiquidity: parseFloat(t.liquidityActive.toString()),
        price0: parseFloat(price0),
        tick: t.tick
      };
      if (chartEntry.activeLiquidity > 0) {
        newData.push(chartEntry);
      }
    }
    return newData;
  }, [data, invertPrices]);
  return useMemo(() => {
    return {
      isLoading,
      error,
      formattedData: !isLoading ? formatData() : void 0
    };
  }, [isLoading, error, formatData]);
}
