const PRICE_FIXED_DIGITS = 8;
const getActiveTick = (tickCurrent, feeAmount, tickSpacing) => tickCurrent && feeAmount && tickSpacing ? Math.floor(tickCurrent / tickSpacing) * tickSpacing : void 0;
const MAX_TICK_FETCH_VALUE = 1e3;
